/* App-related CSS */
@import url('https://fonts.googleapis.com/css?family=Nunito&display=swap');

body {
  background-color: rgb(51, 51, 51);
  font-family: 'Nunito', sans-serif;
}

table, tbody, tr {
  margin: 0 auto;
  width: 100%;
}

header td {
  text-align: center;
}

td {
  margin: 0 auto;
  width: 33%;
}

details > summary::-webkit-details-marker {
  display: none;
}

.page-header {
  text-align: center;
  margin: 20px;
}

.page-title {
  color: white;
  font-size: 48pt;
}

.page-search {
  border-radius: 10px;
  padding: 10px;
}

small {
  color: white;
}

@media (max-width: 430px) {

  img {
    width: 50px;
  }

  summary {
    font-size: 10pt;
  }

}
